.footer__copyright {
    margin: 0;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #545454;
  }
        
@media screen and (max-width: 580px) {
.footer__copyright {
    font-size: 16px;
    line-height: 19px;
  }
}

@media screen and (max-width: 450px) {
.footer__copyright {
    font-size: 14px;
    line-height: 17px;
  }  
}